body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  padding: 0;
  transition: 0.5s;
  margin: 0;

}

body {
  background-color: #F9C61B;
  color: #211E1F;
  min-height: 100vh;
  margin: 0;
  display: grid;
  grid-template-rows: 3fr;
  min-width: 100vw;
}

.loading {
  padding: 10px 30px;
}

.not-found-text {
  text-align: center;
  font-size: 1.25em;
  padding: 20px;
}


  .pokemon-heart-btn {
    flex-direction: column;
    height: 30px;
    background-color: rgba(255, 255, 255, 0.6);
    border-radius: 20px;
    box-shadow: 1px 1px 1px 1px rgba(0, 0, 0, 0.25);
    padding: 4px;
    cursor: pointer;
  }
  
  .icon-heart{
    width: 20px;
    height: 20px;
  }
  
  .icon-redheart{
    color:#FE0000;
  }
footer {
    
    background: #365C9D;
    height: auto;
    width: 100%;
    font-family: "Open Sans";
    padding-top: 40px;
    color: #fff;
  }
  
  .footer-content {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    text-align: center;
  }
  
  .footer-content h3 {
    font-size: 1.8rem;
    font-weight: 400;
    text-transform: capitalize;
    line-height: 3rem;
  }
  
  .footer-content p {
    max-width: 500px;
    margin: 10px auto;
    line-height: 28px;
    font-size: 14px;
  }
  
  .socials {
    list-style: none;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 1rem 0 3rem 0;
  }
  
  .socials li {
    margin: 0 10px;
  }
  
  .socials a {
    text-decoration: none;
    color: #fff;
  }
  
  .icon {
    font-size: 1.1rem;
  }
  
  .icon:hover {
    color: aqua;
    cursor: pointer;
  }
  
  .footer-bottom {
    background: #10336F;
    width: 100%;
    padding: 20px 0;
    text-align: center;
  }
  
  .footer-bottom p {
    font-size: 14px;
    word-spacing: 2px;
    text-transform: capitalize;
  }
  
  .footer-bottom span {
    text-transform: uppercase;
    opacity: .4;
    font-weight: 200;
  
  }
nav {
    
    color: white;
    height: 70px;
    background-color: #365C9D;
    padding: 0 36px 0 36px;
}

.navbar-container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px;
}

.navbar-fixed {
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    z-index: 100;
}

.navbar-img {
    width: 125px;
}

.navbar-link {
    color: white;
    text-decoration: none;
}

.favorite button {
    display: flex;
    grid-gap: 5px;
    gap: 5px;
}

button {
    text-decoration: none;
    font-weight: 500;
    font-size: 16px;
    padding: 9px 25px;
    border: none;
    border-radius: 50px;
    cursor: pointer;
}

.navbar-button {
    background-color: #365C9D;
    color: white;
    border: 1px solid white;
}

.navbar-button:hover {
    background-color: #fff;
    color: #000;
    border: none;
}
.pagination-container {
    display: flex;
    align-items: center;
  }
  
  .pagination-container button {
  
    background-color: rgba(255, 255, 255, 0.6);
    border-radius: 50px;
    cursor: pointer;
    height: 30px;
    border-radius: 20px;
    box-shadow: 1px 1px 1px 1px rgba(0, 0, 0, 0.25);
    padding: 6px;
    margin: 0 10px 0 10px;
  }
  
  .pagination-btn{
    height: 20px;
    width: 20px;
  }
.pokemon-card {
    cursor: pointer;
    height: 150px;
    display: flex;
    box-shadow: 4px 4px 4px rgba(0, 0, 0, 0.25);
    border-radius: 20px;
    padding: 20px;
  }
  
  .pokemon-card:hover {
    -webkit-transform: scale(1.1);
    transform: scale(1.1);
    box-shadow: inset 0 0 0 5px black;
  }
  
  .pokemon-image {
    height: 160px;
  }

  .pokemon-image-container {
    border-radius: 50%;
    background-color: rgba(255, 255, 255, 0.6);
  }

  .link-pokemon {
    text-decoration: none;
    color: black;
  }
  
  .card-body {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 10px 10px 10px 0;
    flex: 1 1;
  }
  
  .card-top {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .card-top h3 {
    font-size: 1.5em;
    text-transform: capitalize;
  }
  
  .card-bottom {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  
  .card-number {
    font-weight: bold;
    background-color: rgba(255, 255, 255, 0.6);
    border-radius: 10px;
    padding: 5px;
  }
  
  .pokemon-type {
    display: flex;
  }
  
  .pokemon-type-text {
    background-color: rgba(255, 255, 255, 0.6);
    border-radius: 20px;
    padding: 10px;
    margin-right: 10px;
    text-transform: capitalize;
  }

.pokedex-header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 0px 20px;
}

.pokedex-grid {
  padding: 50px;
  display: grid;
  grid-gap: 30px;
  gap: 30px;
  grid-template-columns: repeat(3, 1fr);
}

@media (max-width: 1336px){
  .pokedex-grid {
    padding: 50px;
    display: grid;
    grid-gap: 30px;
    gap: 30px;
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 915px){
  .pokedex-grid {
    padding: 50px;
    display: grid;
    grid-gap: 30px;
    gap: 30px;
    grid-template-columns: repeat(1, 1fr);
  }
}

.searchbar-container {
    display: flex;
    margin: 0px auto;
    padding: 20px 20px;
    align-items: center;
  }
  
  .searchbar {
    margin-right: 20px;
    max-height: 79px;
    overflow: hidden;
  }
  
  .searchbar input {
    padding: 10px;
    border-radius: 50px;
  }
  
  .searchbar-btn button {
    border: none;
    border-radius: 50px;
    height: 40px;
    cursor: pointer;
    background-color: #365C9D;
    color: white;
    padding: 10px 12px;
  }
  
  .suggestion {
    background-color: white;
    position: relative;
    border-radius: 50px;
    width: 176px;
    padding: 9px;
    cursor: pointer;
    border-right: 1px solid black;
    border-left: 1px solid black;
    border-bottom: 1px solid black;
  }

  .suggestion:hover {
    background-color: #365C9D;
    color: #fff;
  }
.favorite-header {
    display: flex;
    justify-content: center;
    padding: 20px;
    text-transform: capitalize;
  }
.details-container {
    background-color: white;
    border-radius: 20px;
    padding: 50px;
    margin: 0px 80px 40px 80px;
    height: 100vh;
    display: grid;
    grid-template-columns: 50% 50%;
    grid-row: auto auto;
    grid-column-gap: 20px;
    grid-row-gap: 20px;
    max-width: 1800px;
}

.details-header {
    display: flex;
    justify-content: center;
    padding: 20px;
    text-transform: capitalize;
}

.details-image-container {
    position: relative;
    display: flex;
    justify-content: center;
}

.details-btn-container {
    position: absolute;
    top: 0px;
    right: 0px;
}

.details-image {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 70%;
    height: 100%;
    border-radius: 50%;
    background-color: rgba(255, 255, 255, 0.6);
}

.pokemon-details-image {
    width: 85%;
}

.details-heart-btn {
    height: 50px;
    width: 50px;
    background-color: rgba(255, 255, 255, 0.6);
    border-radius: 50%;
    box-shadow: 1px 1px 1px 1px rgba(0, 0, 0, 0.25);
    padding: 4px;
    cursor: pointer;
}

/*Details DATA */
.details-stats-flex {
    align-items: center;
    display: flex;
    justify-content: space-between;
}

.details-data-type {
    color: white;
    text-transform: uppercase;
    display: inline-block;
    padding: 7px;
    width: 70px;
    text-align: center;
    border: 2px solid;
    border-radius: 10px;
    text-shadow: 1px 1px 2px rgb(0 0 0 / 70%);
}

.details-stats-flex :nth-child(1) {
    margin-right: auto;
}

.details-stats-map {
    display: flex;
}

.details-data-container h2 {
    padding-bottom: 10px;
}

.details-data {
    box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.2);
    border-radius: 10px;
    padding: 25px 30px;
    background-color: #f7f6f0;
}

.details-data hr {
    opacity: 0.2;
}

.details-data h2 {
    margin-bottom: 20px;
}

.details-data div {
    padding: 10px;
}

.details-image {
    display: flex;
    align-items: center;
    justify-content: center;
}

.details-stats h2 {
    padding-bottom: 10px;
}

.details-bar {
    height: auto;
    background-color: #f7f6f0;
    box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.2);
    border-radius: 10px;
    padding: 25px 30px;

}

.details-bar .bar {
    margin: 20px 0;
}

.details-bar .bar:first-child {
    margin-top: 0px;
}

.details-bar .bar .info {
    margin-bottom: 5px;
}

.details-bar .bar .info span {
    font-size: 17px;
    font-weight: 500;
}

.details-bar .bar .progress-line {
    position: relative;
    height: 10px;
    width: 100%;
    background: #f0f0f0;
    -webkit-transform: scaleX(0);
            transform: scaleX(0);
    -webkit-transform-origin: left;
            transform-origin: left;
    box-shadow: inset 0px 1px 1px rgba(0, 0, 0, 0.05),
        0px 1px rgba(255, 255, 255, 0.8);
    -webkit-animation: animate 1s cubic-bezier(1, 0, 0.5, 1) forwards;
            animation: animate 1s cubic-bezier(1, 0, 0.5, 1) forwards;
}

@-webkit-keyframes animate {
    100% {
        -webkit-transform: scaleX(1);
                transform: scaleX(1);
    }
}

@keyframes animate {
    100% {
        -webkit-transform: scaleX(1);
                transform: scaleX(1);
    }
}

.bar .progress-line span {
    height: 100%;
    width: 80%;
    background: #6665ee;
    position: absolute;
    border-radius: 10px;
    -webkit-transform: scaleX(0);
            transform: scaleX(0);
    -webkit-transform-origin: left;
            transform-origin: left;
    -webkit-animation: animate 1s 1s cubic-bezier(1, 0, 0.5, 1) forwards;
            animation: animate 1s 1s cubic-bezier(1, 0, 0.5, 1) forwards;
}

.weakness-type-container h2 {
